@keyframes pulse {
  0% {
    transform: scale(100%);
  }

  100% {
    transform: scale(110%);
  }
}

@keyframes text {
  0% {
    filter: blur(12px);
    opacity: 0;
  }

  100% {
    filter: blur();
    opacity: 1;
  }
}

@keyframes shake-horizontal {
  0%, 100% {
    transform: translateX(0);
  }

  10%, 30%, 50%, 70% {
    transform: translateX(-10px);
  }

  20%, 40%, 60% {
    transform: translateX(10px);
  }

  80% {
    transform: translateX(8px);
  }

  90% {
    transform: translateX(-8px);
  }
}

@keyframes swing {
  0% {
    transform: rotate(6deg);
  }

  4% {
    transform: rotate(-6deg);
  }

  8% {
    transform: rotate(3deg);
  }

  12% {
    transform: rotate(-3deg);
  }

  16% {
    transform: rotate(0);
  }
}

@keyframes expand {
  0% {
    letter-spacing: -.6em;
    opacity: 0;
  }

  50% {
    opacity: .6;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fade-left-to-right {
  from {
    opacity: 0;
    transform: translateX(-250px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fade-bottom-to-top {
  from {
    opacity: 0;
    transform: translateY(150px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slide-top {
  from {
    transform: translateY(-5px);
  }

  to {
    transform: translateY(5px);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes wobble-hor-bottom {
  0%, 100% {
    transform-origin: 40% 70%;
    transform: translateX(0);
  }

  15% {
    transform: rotate(-.8deg);
  }

  30% {
    transform: rotate(.8deg);
  }

  45% {
    transform: rotate(-.6deg);
  }

  60% {
    transform: rotate(.6deg);
  }

  75% {
    transform: rotate(-.1deg);
  }
}

@keyframes fallDown {
  from {
    opacity: 0;
    transform: translateY(-400%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes hover-anim {
  from {
    transform: rotateY(0deg);
  }

  to {
    transform: rotateY(360deg);
  }
}

@keyframes glow {
  0% {
    filter: hue-rotate();
  }

  100% {
    filter: hue-rotate(360deg);
  }
}

@keyframes gelatine {
  from, to {
    transform: scale(1);
  }

  25% {
    transform: scale(.9, 1.1);
  }

  50% {
    transform: scale(1.1, .9);
  }

  75% {
    transform: scale(.95, 1.05);
  }
}

@keyframes stretch {
  0% {
    background-color: #f8d24e;
    transform: scale(1);
  }

  60% {
    background-color: rgba(22, 177, 56, .992);
    transform: scale(1);
  }

  100% {
    background-color: rgba(34, 228, 76, .992);
    transform: scale(1.2);
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translate(-50%);
  }

  40% {
    transform: translate(-50%, 30px);
  }

  60% {
    transform: translate(-50%, 15px);
  }
}

@keyframes wobble {
  0% {
    transform: translateX(0%);
  }

  15% {
    transform: translateX(-5%)rotate(-5deg);
  }

  30% {
    transform: translateX(5%)rotate(3deg);
  }

  45% {
    transform: translateX(-5%)rotate(-3deg);
  }

  60% {
    transform: translateX(5%)rotate(2deg);
  }

  75% {
    transform: translateX(-5%)rotate(-1deg);
  }

  100% {
    transform: translateX(0%);
  }
}

@keyframes shine {
  0% {
    background-position: 0;
  }

  60% {
    background-position: 600px;
  }

  100% {
    background-position: 600px;
  }
}

@keyframes pulse-2 {
  0% {
    box-shadow: 0 0 rgba(255, 0, 0, 0);
  }

  70% {
    box-shadow: 0 0 0 5px rgba(255, 0, 0, .5);
  }

  100% {
    box-shadow: 0 0 rgba(255, 0, 0, 0);
  }
}

